body {
    //background-color:#eceff1;
    background-color: #eee;
    color: #555;
}

a {
    color: #4b6dc6;
    text-decoration: none;

    &:hover {
        text-decoration: none;
        color: #809bf9;
    }

    &:not([href]),&:not([href]):hover {
        color: #666;
        text-decoration: none;
    }
}
nav.navbar{
    width:100%;
    position:fixed;
    top:0;
    z-index:100;

    &.navbar-scroll{
        border-bottom: 1px solid #004395;
        box-shadow: 0 0 5px 4px rgba(0, 0, 0, 0.3);

        .navbar-brand,
        .nav-link {
            color: #4b6dc6;
        }
        .nav-link:hover,.nav-item.active .nav-link{
            color:#545b62 !important;
        }
        
    }

}

footer {
    //text-align: center;
    padding: 1rem 0;
    font-size: 80%;
    color: #6c757d;

    span {
        padding-right: 0.5rem;
    }
}




.main-container{
    position: relative;
    //background-color:white;
    top: 25rem;

}

.img-container{
    position: absolute;
    left:0;
    right:0;
    padding:0;
    //z-index:-1;
    .carousel {
        height: 30rem;

        top: 0;
        //z-index: -1;
        left: 0;

        //padding:0 15px;
        .carousel-inner {
            height: 100%;

            .carousel-item {
                height: 100%;
                background-size: cover;
                background-position: center;
                background-repeat: no-repeat;
                z-index:-1;
            }
        }
    }

}

.main-shade {
    margin: 0 -15px;

    .main-shade-svg {

        height: 5rem;

        .main-shade-path {
            fill: white
        }
    }
}

.home {

    padding: 2rem;
    background-color: white;
    justify-content: space-between;
    flex-wrap: wrap-reverse;
    
    .info-container{
       width: 100%;
        h3,h4,h5{
            color:#4b6dc6;
        }
    }
    .paper-container{
        padding:0;
        border:none;
    }
    .project-container{
        width: 100%;
        border:none;
        margin-top:2rem;
        padding:0;
        ul{
            list-style-type: none;
            padding:0;
            li{
                border-top:1px solid #aaa;
                p{
                    word-break: break-all;
                    &:first-child{
