.paper-container {

    /*h4{
        padding-left:1rem;
    }
    .title{
        padding:5rem 0 1rem 1rem;
    }*/
    .paper-item {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        .paper-item-title{
            word-break:break-all;
        }
    }

    .paper-item-link {
        flex-basis: 8rem;
        text-align: center;
    }

    .list-group {
        padding: 2rem 0;
    }

    .card {
        //background-color: #f4f1f1;

        box-shadow: 0 0 3px 2px rgba(0, 0, 0, 0.1);
        min-width: 15rem;
        margin-bottom: 15px;

        span.badge {
            color: #fff;
            background-color: #4b6dc6;
        }
    }
}

.user-container {

    /*.title{
        padding:0 0 1rem 1rem;
    }
    h4{
        padding-left:1rem;
    }*/
    h4,h5{
        padding-bottom: 1rem;
    }

    .card-deck {
        justify-content: space-around;
    }
}

.user-item.card {
    width: 12rem;
    flex: 0 1 auto;
    margin-bottom: 15px;
    align-items: center;
    background-color: transparent;
    border: none;

    .card-img-top {
        width: 8rem;
        height: 8rem;
        //border:1px solid rgba(0,0,0,.125);
        box-shadow: 0 0 3px 2px rgba(0, 0, 0, 0.1);
        transition: box-shadow .5s ease-in-out,
            border-radius .5s ease-in-out;

        &:hover {
            box-shadow: 0 0 5px 4px rgba(0, 0, 0, 0.2);
            border-radius: 10% !important;
        }
    }
}

.user-single {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    min-height: 35rem;

    .card-body {
        text-align: center;
    }

    dd {
        max-width: 35rem;
    }
}


.patent-container{
    ul{
        list-style-type: circle;
    }
    li{
        display: list-item;
    }
    .title{
        color:#4b6dc6;
        
    }
    span{
        margin-right:15px;
    }
    
}

.bg-grey {
    background-color: #eceff1;
}

//加载动画
@keyframes placeHolderShimmer{
    0%{
        width:0%;
        opacity:0;
    }
    50%{
        width:50%;
        opacity: 1;
    }
    100%{
       width:100%;
       opacity: 0;
    }
}

.content-loader {
    border: 1px solid #eceff1;
    margin-bottom: 15px;
    
  
}
.content-loader-rect{
    background-color: #f1f1f1;
    div{
        height:100%;
    }
}
.animated-bg{
        background:linear-gradient(to right,#fcfcfc,#d6d6d6,#e3e3e3);
        animation-duration: 1s;
        animation-fill-mode: forwards;
        animation-iteration-count: infinite;
        animation-name: placeHolderShimmer;
        animation-timing-function: linear;
    }
.content-loader-circle{
    position: relative;
    overflow: hidden;
    background-color: #f1f1f1;
    div{
        
        height:100%;
    }
}

.animated-circle{
    position: absolute;
        background:linear-gradient(to right,#fcfcfc,#d6d6d6,#e3e3e3);
        animation-duration: 1s;
        animation-fill-mode: forwards;
        animation-iteration-count: infinite;
        animation-name: placeHolderShimmer;
        animation-timing-function: linear;
}

.animated-bg {
    background: linear-gradient(to right, #f3f3f3, #ebebeb);
    animation-duration: 1s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-name: placeHolderShimmer;
    animation-timing-function: linear;
}

